  /* Contact End */
  
  /* Footer Start */
  /* Footer Starts */
  .footer--container {
    display: flex;
    padding: 26.667px 45.333px;
    flex-direction: column;
    align-items: flex-start;
    background: var(--primary);
    align-items: center;
